import onlineFormField from "../onlineFormField.json";
import oncampusFormField from "../oncampusFormField.json";

export default function getLink(data, school, checked, oncampus) {
    function get(field) {
        return data.get(field).trim();
    }

    const parent_first = get("parent_first");
    const parent_last = get("parent_last");
    const parent_middle = get("parent_middle");
    const parent_email = get("parent_email");
    const parent_phone = get("parent_phone");
    const parent_street = get("parent_street");
    const parent_city = get("parent_city");
    const parent_state = get("parent_state");
    const parent_postal_code = get("parent_postal_code");
    const parent_address = `${parent_street}, ${parent_city}, ${parent_state} ${parent_postal_code}`;

    const student_first = get("student_first");
    const student_last = get("student_last");
    const student_middle = get("student_middle");
    const student_street = checked ? "" : get("student_street");
    const student_city = checked ? "" : get("student_city");
    const student_state = checked ? "" : get("student_state");
    const student_postal_code = checked ? "" : get("student_postal_code");
    const student_address = `${student_street}, ${student_city}, ${student_state} ${student_postal_code}`;
    const student_birthdate = get("student_birthdate");

    const parent_name = `${parent_first} ${parent_middle} ${parent_last}`;
    const student_name = `${student_first} ${student_middle} ${student_last}`;
    const school_name = school;

    const online_base =
        "https://na2.docusign.net/Member/PowerFormSigning.aspx?PowerFormId=4c88591d-e670-44f2-9967-c950adc0ccea&env=na2&acct=a76475db-2ab3-4a5f-b7bd-1ba6a5dd7f4e&v=2";

    const oncampus_base =
        "https://na2.docusign.net/Member/PowerFormSigning.aspx?PowerFormId=6947fa0f-94b1-4424-98ee-ab8b2fc4a6ac&env=na2&acct=a76475db-2ab3-4a5f-b7bd-1ba6a5dd7f4e&v=2";

    if (oncampus) {
        const formData = {
            parent_name: parent_name,
            student_first: student_first,
            student_last: student_last,
            student_middle: student_middle,
            parent_first: parent_first,
            parent_last: parent_last,
            parent_middle: parent_middle,
            minor_name: student_name,
            parent_phone: parent_phone,
            parent_address: parent_address,
            student_address: checked ? parent_address : student_address,
            student_birthdate: student_birthdate,
            releasor_name: student_name,
            guardian_name: parent_name,
            covid_parent_name: parent_name,
        };

        let oncampus_link =
            oncampus_base +
            `&Parent/Guardian_UserName=${parent_first} ${parent_middle} ${parent_last}` +
            `&Parent/Guardian_Email=${parent_email}` +
            `&Chaperone=${school_name}`;

        for (const key in oncampusFormField) {
            oncampus_link += `&${oncampusFormField[key]}=${formData[key]}`;
        }

        return oncampus_link.replace(" ", "%20");
    } else {
        const formData = {
            student_first: student_first,
            student_last: student_last,
            student_middle: student_middle,
            parent_first: parent_first,
            parent_last: parent_last,
            parent_middle: parent_middle,
            minor_name: student_name,
            parent_phone: parent_phone,
            parent_address1: parent_address,
            parent_address2: parent_address,
            student_address: checked ? parent_address : student_address,
            student_birthdate: student_birthdate,
        };

        let online_link =
            online_base +
            `&Parent/Guardian_UserName=${parent_first} ${parent_middle} ${parent_last}` +
            `&Parent/Guardian_Email=${parent_email}` +
            `&Text ee891429-fe1b-48b4-b873-d4a32fafcf59=MIT Science Olympiad (School: ${school_name})`;

        for (const key in onlineFormField) {
            online_link += `&Text ${onlineFormField[key]}=${formData[key]}`;
        }

        return online_link.replace(" ", "%20");
    }
}
