import React, { useState, Fragment } from "react";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Container from "@mui/material/Container";
import Card from "@mui/material/Card";
import { Typography, useTheme, useMediaQuery } from "@mui/material";

import FormInfo from "../components/FormInfo";
import logo from "../scioly_logo.png";

export default function SignForm() {
    const [submitted, setSubmitted] = useState(false);

    const toggleSubmitted = () => {
        setSubmitted(!submitted);
    };

    const theme = useTheme();
    const mdOrLarger = useMediaQuery(theme.breakpoints.up("md"));
    const smOrLarger = useMediaQuery(theme.breakpoints.up("sm"));
    const xsmOrLarger = useMediaQuery(theme.breakpoints.up("xsm"));

    return (
        <Fragment>
            <Box
                sx={{
                    display: "flex",
                    flexDirection: "column",
                    alignItems: "center",
                    mt: 10,
                    mb: 3,
                }}
            >
                <Box
                    sx={{
                        display: "flex",
                        mt: 2,
                        mx: "auto",
                        width: "160px",
                        "& img": {
                            width: "100%",
                        },
                    }}
                >
                    <img src={logo} alt="MIT Science Olympiad Logo" />
                </Box>
                {mdOrLarger ? (
                    <h2>MIT Science Olympiad 2022: Release Form</h2>
                ) : smOrLarger ? (
                    <Fragment>
                        <h1>MIT Science Olympiad 2022</h1>
                        <h2>Release Form</h2>
                    </Fragment>
                ) : xsmOrLarger ? (
                    <Fragment>
                        <h1>MIT Science Olympiad</h1>
                        <h2>Release Form</h2>
                    </Fragment>
                ) : (
                    <h2>Release Form</h2>
                )}
            </Box>
            <Container component="main" maxWidth="md">
                <Box mb={20}>
                    <Card variant="outlined" mb={5}>
                        <Box
                            sx={{
                                display: "flex",
                                flexDirection: "column",
                                alignItems: "center",
                                mt: 3,
                                mb: 3,
                                ml: 3,
                                mr: 3,
                            }}
                        >
                            {!submitted ? (
                                <Fragment>
                                    <FormInfo
                                        submitted={submitted}
                                        toggleSubmitted={toggleSubmitted}
                                    />
                                </Fragment>
                            ) : (
                                <Fragment>
                                    <h5>Once you complete the DocuSign form, you are all set!</h5>
                                    <Button
                                        variant="outlined"
                                        onClick={toggleSubmitted}
                                    >
                                        <Typography>
                                            Submit another
                                        </Typography>
                                    </Button>
                                </Fragment>
                            )}
                        </Box>
                    </Card>
                </Box>
            </Container>
        </Fragment>
    );
}
